/* width */

::-webkit-scrollbar {
  width: 7px;
}


/* Track */

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 7px;
}


/* Handle */

::-webkit-scrollbar-thumb {
  background: #e66f31;
  border-radius: 7px;
}


/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
  background: #3f82d9;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}


